import React, { useState, useEffect } from "react";
import { Container, DateText, DateContainer } from "./WykresyStyle";
import $ from "jquery";
import { Chart } from "react-charts";
import useDemoConfig from "./useDemoConfig";
import ResizableBox from "./ResizableBox";
import DatePicker from "react-datepicker";

import "react-datepicker/dist/react-datepicker.css";
export default function Wykresy() {
  const [datyPomiaru, setDatyPomiaru] = useState([]);
  const [wyniki, setWyniki] = useState([]);
  const [startDate, SetStartDate] = useState(new Date());
  const [endDate, SetEndDate] = useState(new Date());
  const [chartReady, SetChartReady] = useState(false);
  const [filterArray, SetFilteredArray] = useState([]);
  
  useEffect(() => {
    // Zaktualizuj tytuł dokumentu korzystając z interfejsu API przeglądarki
    getData();
  });
  function getData() {
    $.ajax({
      type: "GET" /*Informacja o tym, że dane będą pobierane*/,
      url:
        "http://localhost/phpsandbox/pobierz.php" /*Informacja, o tym jaki plik będzie przy tym wykorzystywany*/,
      contentType:
        "application/json; charset=utf-8" /*Informacja o formacie transferu danych*/,
      dataType: "json" /*Informacja o formacie transferu danych*/,

      /*Działania wykonywane w przypadku sukcesu*/
      success: (json) => {
        /*Funkcja zawiera parametr*/
        let data2 = [];
        let data1 = [];
        let data3 = [];
        var data4 = [];
        /*Pętla typu for...in języka Javascript na danych w formacie JSON*/
        for (var klucz in json) {
          var wiersz =
            json[klucz]; /*Kolejne przebiegi pętli wstawiają nowy klucz*/
          var id1 = wiersz[0];
          var id = wiersz[1];

          /*Ustalenie sposobu wyświetlania pobranych danych w bloku div*/
          //console.log(id, id1);
          if (id1.indexOf("-00-") < 0) {
            data2[klucz] = id;
            data1[klucz] = id1;
            data3.push(id1, id);
            data4.push(data3);
            data3 = [];
          }

          // console.log(data4);
        }
        updateStates(data4);
        //console.log(data3);

        /*Dezaktywacja na określony czas przycisku wysyłającego - ten krok można pomninąć*/
        $("#pobierz").attr("disabled", true);
        setTimeout(function () {
          $("#pobierz").attr("disabled", false);
        }, 10000);
      },

      /*Działania wykonywane w przypadku błędu*/
      error: function (blad) {
        alert("Wystąpił błąd");
        console.log(
          blad
        ); /*Funkcja wyświetlająca informacje 
            o ewentualnym błędzie w konsoli przeglądarki*/
      },
    });
  }
  function updateStates(data4) {
    setDatyPomiaru(data4);
    //console.log(new Date("2020-05-2 13:43:33"));
  }
  function generateWykres(){
    
  }

  const data = React.useMemo(
    () => [
      {
        label: "Series 1",
        data: filterArray.map(function(x) {
          x[0]=new Date(x[0]);
          return x;
      }),
      },
    ],
    []
  );





  const series = React.useMemo(
    () => ({
      showPoints: false,
    }),
    []
  );
  const axes = React.useMemo(
    () => [
      { primary: true, type: "time", position: "bottom" },
      { type: "linear", position: "left" },
    ],
    []
  );
  const przygotujDane = () => {
    // console.log(datyPomiaru);
    SetChartReady(true);

  };
  function handleChange(date) {
    SetStartDate(date);



    
    /*
  this.setState({
    startDate: date
  });*/
    //console.log(date);
  }
  function handleChange2(date) {
    SetEndDate(date);
    /*
  this.setState({
    startDate: date
  });*/
    let filteredArray = [];
    //  console.log(date.toISOString().replace(/T/g, "-"));
    datyPomiaru.forEach(function (item) {
      //filteredArray.push(item * item);
      if (item[0] < date.toISOString().replace(/T/g, "-")) {
        // console.log(item,date.toISOString().replace(/T/g, "-"));
       item[0] = new Date(item[0]);
       item[1]=parseInt(item[1], 10);
        if(filteredArray.length<5)
        filteredArray.push(item);
        //  console.log(filteredArray);
      }
     
      SetFilteredArray(filteredArray);
      // data[0]["data"]=filteredArray;
      //console.log(data[0]["data"]);
    });
    console.log(data[0]);
  }
  return (
    <div>
      <ResizableBox>
      {chartReady ? <Chart data={data} series={series} axes={axes} tooltip /> : null}  
        <button className="dupa" onClick={() => przygotujDane()} />
      </ResizableBox>

      <DateContainer>
        <DateText>Enter start date</DateText>
        <DatePicker onChange={handleChange} selected={startDate} />
      </DateContainer>
      <DateContainer>
        <DateText>Enter end date</DateText>
        <DatePicker onChange={handleChange2} selected={endDate} />
      </DateContainer>
    </div>
  );
}
